<template>
    <v-container fluid style="">
        <v-card color="primary" style="margin-bottom:30px" v-for="faq in baboharbidhi" :key="faq.id">
            <v-card-title>
                <h5><v-icon>tag</v-icon>{{ faq.title }}</h5>
            </v-card-title>
            <v-card-text>{{ faq.discription }}</v-card-text>
        </v-card>
        <v-snackbar v-model="snackbar" :vertical="true" :bottom="'bottom'" :right="'right'" :timeout="10000">
            {{ alertMessage }}
            <v-btn class="snackButton" color="primary" flat @click="snackbar = false">Close</v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
import { mapState } from "vuex";
import axios from "../../axios_instance";

export default {
    data() {
        return {
            snackbar: null,
            alertMessage: null,
            baboharbidhi: []
        };
    },
    methods: {
        async getBaboharbidhi() {
            let loader = this.$loading.show();
            let response = await axios.get("/baboharbidhi");
            this.baboharbidhi = response.data;
            loader.hide();
        }
    },
    created() {
        this.getBaboharbidhi();
    }
};
</script>