import { localStorageService } from "./helper.js";

const axios = require("axios");
const defaultOptions = {
  // baseURL: process.env.VUE_APP_API_URL
  // baseURL: 'https://staging.mistrimama.com/backend/api/',
   baseURL: 'http://dev.mistrimama.com/backend/api/',
  // baseURL: 'http://localhost/mistrimama/backend/api/',
  // baseURL: 'http://localhost/mistrimama-backend-V3/api/',
  // baseURL: 'http://localhost/mistrimama-backend-V3/api/',
  // baseURL: 'https://testv3.mistrimama.com/backend/api/',
  // baseURL: 'https://mistrimama.com/backend/api/',
  //baseURL: 'http://127.0.0.1:8000/api/',
  headers: {
    "Content-Type": "application/json"
  }
};
let axiosInstance = axios.create(defaultOptions);
axiosInstance.interceptors.request.use(function(config) {
  let token = localStorageService.getItem("d_token");
  config.headers.Authorization = token ? `Bearer ${token}` : "";
  return config;
});

export default axiosInstance;
