<template>
   <div class="auto-container" style="margin-top:100px">
        <section class="specialize-section-two alternate">
            <div class="row">
                <div class="col-lg-6 col-md-6">
                    <div class="title-column">
                        <div class="inner-column">
                            <div class="sec-title">
                                <h2>Refer and Get Paid</h2>
                            </div>
                            <div class="text-box">
                                <p class="text-justify">Are you a social magnet? Earn some money by telling your friends or family about Mistri Mama services.</p>
                                <p class="text-justify">Get started by creating a Mistri Mama user account. Spread the word by sharing your unique referral code or referral link through blog posts, articles, emails, Facebook posts and tweets. You’ll get paid for every single service completion through your referral code or referral link</p>
                            </div>
                        </div>
                    </div>
                    <div class="title-column">
                        <div class="inner-column">
                            <div class="sec-title">
                                <h2>Why Refer us?</h2>
                            </div>
                            <div class="text-box">
                                <ul style="margin: 0; padding: 0">
                                    <li>Anyone can participate</li>
                                    <li>Easy and hustle free refer process</li>
                                    <li>Referrer get reward point by every successful refer</li>
                                    <li>Referrer can use this points to get discount on Mistri Mama services</li>
                                    <li>Referrer can also convert this point to cash</li>
                                    <li>Easy and fast cash out process</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div class="title-column">
                        <div class="inner-column">
                            <div class="sec-title">
                                <h2>How to Earn Money</h2>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-6 col-md-6">
                    <div class="inner-column">
                        <div class="discount-form">
                            <v-form ref="signUpwithMFSnumber" v-model="valid" lazy-validation @submit.prevent="signUpwithMFSnumberStore">
                                <v-text-field color="accent" v-model="signUpwithMFSnumber.name" :error-messages="display_errors.name" type="text" label="Full Name *" outlined></v-text-field>
                                <v-text-field color="accent" v-model="signUpwithMFSnumber.phone"  prefix="+88" placeholder="01XXXXXXXXX" :error-messages="display_errors.phone" type="number" label="Phone *" outlined></v-text-field>
                                <v-text-field color="accent" v-model="signUpwithMFSnumber.email" :error-messages="display_errors.email" type="text" label="Email" outlined></v-text-field>
                                <v-select color="accent" v-model="signUpwithMFSnumber.area" :items="cluster" :error-messages="display_errors.area" :item-text="'name'" :item-value="'id'" label="Area *" append-icon="keyboard_arrow_down" outlined></v-select>
                                <v-select color="accent" :items="itemsMfs" v-model="signUpwithMFSnumber.mfs_type" :error-messages="display_errors.mfs" label="Select Service *"></v-select>
                                <v-text-field color="accent" v-model="signUpwithMFSnumber.mfs_no"  prefix="+88" placeholder="01XXXXXXXXX" :error-messages="display_errors.phone" type="number" label="MFS number *" outlined></v-text-field>
                                <v-btn color="primary" type="submit">Submit</v-btn>
                            </v-form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section class="services-section">
            <div class="row">
                <div class="col-lg-3 col-md-6">
                    <div class="services-box">
                        <div class="service-block" style="padding:0">
                            <div class="inner-box">
                                <div class="image-box">
                                    <figure class="image">
                                        <img style="width: 100%" class="img-responsive" src="https://mistrimama.com/backend/public/frontend/image/refer_img/01.jpg" alt="MM">
                                    </figure>
                                </div>
                                <div class="lower-content">
                                    <h3>Become a Referrer</h3>
                                    <div class="text">It's easy and free to join. Just register with your name, mobile number and valid Mobile banking number to get start from today. Mobile banking number is required for cash out process.</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div class="col-lg-3 col-md-6">
                    <div class="services-box">
                        <div class="service-block" style="padding:0">
                            <div class="inner-box">
                                <div class="image-box">
                                    <figure class="image">
                                        <img style="width: 100%" class="img-responsive" src="https://mistrimama.com/backend/public/frontend/image/refer_img/02.jpg" alt="MM">
                                    </figure>
                                </div>
                                <div class="lower-content">
                                    <h3>Refer Someone</h3>
                                    <div class="text">Start sharing referral code or the referral link to others, who is looking for a service. Referrer can share his/her refer code or the referral link to any User through social media or a simple SMS.</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div class="col-lg-3 col-md-6">
                    <div class="services-box">
                        <div class="service-block" style="padding:0">
                            <div class="inner-box">
                                <div class="image-box">
                                    <figure class="image">
                                        <img style="width: 100%" class="img-responsive" src="https://mistrimama.com/backend/public/frontend/image/refer_img/03.jpg" alt="MM">
                                    </figure>
                                </div>
                                <div class="lower-content">
                                    <h3>Earn Reward Point</h3>
                                    <div class="text">User can avail any Mistri Mama service by using the referral code or the link. For every successful service completion, referrer will receive 30% reward points of the actual bill in his/her accounts.</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-lg-3 col-md-6">
                    <div class="services-box">
                        <div class="service-block" style="padding:0">
                            <div class="inner-box">
                                <div class="image-box">
                                    <figure class="image">
                                        <img style="width: 100%" class="img-responsive" src="https://mistrimama.com/backend/public/frontend/image/refer_img/04.jpg" alt="MM">
                                    </figure>
                                </div>
                                <div class="lower-content">
                                    <h3>Get Paid</h3>
                                    <div class="text">Referrer can convert his/her earned reward points into cash and easily cash out through his/her enlisted mobile banking number. To cash out referrer will require 600 reward points.</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section class="specialize-section-two alternate" style="padding-left: 15px; padding-right: 15px;">
            <div class="title-column">
                <div class="inner-column">
                    <div class="sec-title">
                        <h2>Reward Point Policy</h2>
                    </div>
                    <div class="text-box">
                        <p class="text-justify">Referrer will get 30% of RP (reward point) of each successful referred service’s payable amount. (e.g.- Mr. X referred AC detergent service, BDT 1000 to Mr. Y. After successful service, Mr. X will get 30% or 300 reward point). 3 reward points is equivalent to BDT 1. Referrer can withdraw his/her amount, if he/she has minimum 600 reward points, which is equivalent to BDT 200. User or referrer can make service payment through reward point. Only half of total service amount can be adjusted through reward point.</p>
                    </div>
                </div>
            </div>
        </section>
        <v-snackbar v-model="snackbar" :vertical="true" :bottom="'bottom'" :right="'right'" :timeout="10000">
            {{ alertMessage }}
            <v-btn class="snackButton" color="primary" flat @click="snackbar = false">Close</v-btn>
        </v-snackbar>
    </div>
</template>

<script>
    import {
        required, numeric
    }
    from "vuelidate/lib/validators";
    import axios from "../axios_instance.js";
    import {
        localStorageService
    }
    from "../helper.js";

    export default {
        inject: ["theme"],
        data() {
            return {
                categories: [],
                valid: null,
                display_errors: [],
                cluster: [],
                itemsMfs: ["Bkash"],
                signUpwithMFSnumber: {
                    name: null,
                    phone: null,
                    email: null,
                    area: null,
                    mfs_type: "Bkash",
                    mfs_no: null,
                },
                // nameRules: [
                //     v => !!v || "This field cannot be empty",
                //     v => (v && v.length <= 20) || "Name must be less than 20 characters"
                // ],
                // phoneRules: [
                //     v => !!v || "This field cannot be empty", 
                //     v => v.match(/(^(\+88|0088)?(01){1}[3456789]{1}(\d){8})$/)|| "Invalid mobile number (+8801XXXXXXXXX)",
                // ],
                // emailRules: [
                //     v => /.+@.+/.test(v) || 'E-mail must be valid'
                // ],
                alertMessage: null,
                snackbar: false,
            };
        },
        created() { 
            this.getLocations();
            this.storeCategories();
        },
        computed: {
        },
        methods: {
            async getLocations()
            {
                let response = await axios.get("division");
                this.cluster = response.data.data[0].cluster;
            },
            async storeCategories() {
                if (!localStorageService.getItem("categorys")) {
                    let categories = await axios.get("/category");
                    localStorageService.setItem("categorys", categories.data.data);
                    this.categories = categories.data.data;
                } else {
                    this.categories = localStorageService.getItem("categorys");
                }
            },
            async signUpwithMFSnumberStore() {
                if (this.$refs.signUpwithMFSnumber.validate()) {
                    this.display_errors = [];
                    try {
                        let response = await axios.post("/sign-up-with-mfs-number", this.signUpwithMFSnumber);
                        this.alertMessage = response.data.message;
                        this.snackbar = true;
                        this.signUpwithMFSnumber = {
                            name: null,
                            phone: null,
                            email: null,
                            area: null,
                            service_categoris: [],
                            other_service: null,
                        };
                    } catch (error) {
                        if(error.response.data.errors)
                        {
                            this.display_errors = error.response.data.errors;
                        }
                        this.alertMessage = error.response.data.message;
                        this.snackbar = true;
                    }
                }
            },
        }
    };
</script>