<template>
    <div class="auto-container" style="margin-top:100px">
        <section class="specialize-section-two alternate">
            <div class="service-detail">
                <div class="inner-box">
                    <div class="text text-center">
                        <strong>A passionate team of employees is the foundation of a successful startup. They become natural advocates and work tirelessly to see the shared company vision come to life. As in life, anything that starts with a weak foundation will struggle to grow. If you have the passion to contribute a growing startup than join with us. To set new standards of ethics and excellence in delivering to our customers superior quality and value-for-money residential and commercial spaces by employing a team of highly motivated and focused professionals.</strong>
                    </div>
                </div>
            </div>
        </section>
        <hr>
        <section class="specialize-section-two alternate">
            <div class="title-column">
                <div class="inner-column text-center">
                    <div class="sec-title">
                        <h2>Open Positions</h2>
                    </div>
                    <div class="text-box">
                        <p class="text-center">We’re looking for people to join the team who are as excited as we are to help build the platform that empowers the future generation of creators to be successful online.</p>
                    </div>
                </div>
            </div>
        </section>
        <div class="row">
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Marketing & Business Development</div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Operation</div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Call Center</div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Technician</div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Sales</div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-md-6">
                <div class="help-box-two">
                    <div class="inner">
                        <h2>0</h2>
                        <div class="text">Account & Finance</div>
                    </div>
                </div>
            </div>
        </div>
        <section class="specialize-section-two alternate">
            <div class="title-column">
                <div class="inner-column text-center">
                    <div class="sec-title">
                        <h2>Check Opportunity &amp; Apply</h2>
                    </div>
                    <v-form ref="careerForm" v-model="valid" lazy-validation @submit.prevent="careerStore">
                        <v-card elevation="0" style="background-color: transparent">
                            <v-card-text>
                                <v-layout row wrap>
                                    <v-flex md6>
                                        <v-text-field  color="accent" v-model="careerForm.name" :error-messages="display_errors.name" type="text" label="Full Name *" outlined></v-text-field>
                                    </v-flex>
                                    <v-flex md6>
                                        <v-text-field color="accent" prefix="+88" placeholder="01XXXXXXXXX" v-model="careerForm.phone_number" :error-messages="display_errors.phone_number" type="number" label="Phone Number *" outlined></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row wrap>
                                    <v-flex md6>
                                        <v-text-field color="accent" v-model="careerForm.email" :error-messages="display_errors.email" type="text" label="Email *" outlined></v-text-field>
                                    </v-flex>
                                    <v-flex md6>
                                        <v-text-field color="accent" v-model="careerForm.year_of_experience" :error-messages="display_errors.year_of_experience" type="text" label="Year Of Experience" outlined></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row wrap>
                                    <v-flex md4>
                                        <v-text-field color="accent" v-model="careerForm.position" :error-messages="display_errors.position" type="text" label="Position *"  outlined></v-text-field>
                                    </v-flex>
                                    <v-flex md4>
                                        <v-text-field color="accent" v-model="careerForm.salary_expectation" :error-messages="display_errors.salary_expectation" type="number" label="Salary Expectation" outlined></v-text-field>
                                    </v-flex>
                                    <v-flex md4>
                                        <v-text-field color="accent" v-model="careerForm.link" :error-messages="display_errors.link" type="text" label="Website/LinkedIn/Portfolio link" outlined></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-text-field color="accent" label="Upload File *" @click='onButtonClick' :error-messages="display_errors.file" v-model='buttonText' prepend-icon='attach_file' outlined></v-text-field>
                                <input ref="uploader" class="d-none" type="file" accept=".doc,.docx,.pdf" @change="onFileChanged">

                                <v-textarea style="margin-top:15px" color="accent" v-model="careerForm.comments" :error-messages="display_errors.comments" label=" Tell us why you want to work for mistrimama" placeholder=" Tell us why you want to work for mistrimama"></v-textarea>
                            </v-card-text>
                            <v-card-actions>
                                <v-btn color="primary" type="submit">Submit</v-btn>
                                <v-spacer></v-spacer>
                            </v-card-actions>
                        </v-card>
                    </v-form>
                </div>
            </div>
        </section>
        <v-snackbar v-model="snackbar" :vertical="true" :bottom="'bottom'" :right="'right'" :timeout="10000">
            {{ alertMessage }}
            <v-btn class="snackButton" color="primary" flat @click="snackbar = false">Close</v-btn>
        </v-snackbar>
    </div>
</template>

<script>
    import {
        required, numeric
    }
    from "vuelidate/lib/validators";
    import axios from "../axios_instance.js";
    import {
        localStorageService
    }
    from "../helper.js";

    export default {
        inject: ["theme"],
        data() {
            return {
                valid: null,
                otpCode: null,
                otp: false,
                page: "",
                pageBanner: "",
                display_errors: [],
                cluster: [],
                defaultButtonText: '',
                selectedFile: null,
                isSelecting: false,
                careerForm: {
                    name: "",
                    phone_number: "",
                    email: "",
                    year_of_experience: "",
                    salary_expectation: "",
                    link: "",
                    comments: "",
                    file: "",
                    position: ""
                   
                },
                // nameRules: [
                //     v => !!v || "This field cannot be empty",
                //     v => (v && v.length <= 20) || "Name must be less than 20 characters"
                // ],
                //  phoneRules: [
                //     v => !!v || "This field cannot be empty", 
                //     v => v.match(/(^(\+88|0088)?(01){1}[3456789]{1}(\d){8})$/)|| "Invalid mobile number (+8801XXXXXXXXX)",
                   
                // ],
                // emailRules: [
                //     v => /.+@.+/.test(v) || 'E-mail must be valid'
                // ],
                
                passShow: null,
                alertMessage: null,
                snackbar: false,
            };
        },
        created() { 
        },
        computed: {
            buttonText() {
                return this.selectedFile ? this.selectedFile.name : this.defaultButtonText
            }
        },
        methods: {
            onButtonClick() {
                this.isSelecting = true
                window.addEventListener('focus', () => {
                    this.isSelecting = false
                }, { once: true })

                this.$refs.uploader.click()
            },
            onFileChanged(e) {
                this.selectedFile = e.target.files[0];
                this.careerForm.file = this.selectedFile;
            },
            async careerStore() {
                if (this.$refs.careerForm.validate()) {
                    this.display_errors = [];
                    try {
                        const formData = new FormData();
                        formData.append('file', this.selectedFile);
                        formData.append('name', this.careerForm.name);
                        formData.append('phone_number', this.careerForm.phone_number);
                        formData.append('email', this.careerForm.email);
                        formData.append('year_of_experience', this.careerForm.year_of_experience);
                        formData.append('salary_expectation', this.careerForm.salary_expectation);
                        formData.append('link', this.careerForm.link);
                        formData.append('comments', this.careerForm.comments);
                        formData.append('position', this.careerForm.position);

                        let response = await axios.post("/careers", formData, {
                            headers: {
                                'Content-Type': 'multipart/form-data'
                            }
                        }); 
                        
                        this.careerForm = {
                            name: "",
                            phone_number: "",
                            email: "",
                            year_of_experience: "",
                            salary_expectation: "",
                            link: "",
                            comments: "",
                            file: "",
                            position: ""
                        
                        },
                        this.alertMessage = response.data.message;
                        this.snackbar = true;
                    } catch (error) {
                        console.log(error);
                        if(error.response.data.errors)
                        {
                            this.display_errors = error.response.data.errors;
                        }
                        this.alertMessage = error.response.data.message;
                        this.snackbar = true;
                    }
                }
            },
           
         
        }
    };
</script>